<i18n>
{
  "en": {
    "SuccessfulPayment": "Successful payment",
    "SuccessfullyReturned": "Successfully returned the battery",
    "Consumption": "Consumption",
    "Unit": "(Yuan)",
    "cost": "{count}Yuan",
    "TheLeaseTime": "The lease time",
    "time": "{0}days, {1}hours, {2}minutes.",
    "ShopAddress": "Shop address",
    "address": "1 floor, building 3, garden district.",
    "Complete": "Complete"
  },
  "zh": {
    "SuccessfulPayment": "成功支付",
    "SuccessfullyReturned": "成功归还充电宝",
    "Consumption": "消费",
    "Unit": "（元）",
    "cost": "{count}元",
    "TheLeaseTime": "租借时长",
    "time": "{0}天{1}小时{2}分钟",
    "ShopAddress": "店铺位置",
    "address": "花园小区3号楼1层",
    "Complete": "完成"
  }
}
</i18n>

<template>
  <div class="restituteSuccessfully">
    <head-top :title='$t("SuccessfulPayment")' :go-back="true"></head-top>
    <div class="container">
      <img src="../../../assets/scanning_images/succeed.png" alt="">
      <span>{{ $t("SuccessfullyReturned") }}</span>
      <ul class="list">
        <li>
          <span>{{ $t("Consumption") }}<b>{{ $t("Unit") }}</b></span>
          <strong>{{ $t("cost",{count: count}) }}</strong>
        </li>
        <li>
          <span>{{ $t("TheLeaseTime") }}</span>
          <strong>{{ $t("time",timers) }}</strong>
        </li>
        <li>
          <span>{{ $t("ShopAddress") }}</span>
          <strong>{{adress }}</strong>
        </li>
      </ul>
      <button @click="goHandle" class="btn">{{ $t("Complete") }}</button>
    </div>
  </div>
</template>

<script>
import headTop from '../../../components/Header/index'
import {setStorage, getStorage} from '../../../utils/local'

export default {
  name: 'restituteSuccessfully',
  data () {
    return {
      count: 0,
      timers: [0, 0, 0],
      adress: ''
    }
  },

  components: {
    headTop
  },
  mounted () {
    let locaBillinfo = JSON.parse(getStorage('biiinfo'))
    // console.log(this.$route.params)
    if (locaBillinfo) {
      this.count = parseFloat((locaBillinfo.fee / 100).toFixed(2))
      this.adress = locaBillinfo.return_adress
      let d, h, m
      let ldate = new Date(locaBillinfo.lent_time)
      let Rdate = new Date(locaBillinfo.return_time)
      let times = ldate.getTime() - Rdate.getTime()
      // console.log(times)
      d = parseInt(times / 1000 / 3600 / 24)
      times = times - d * 1000 * 3600 * 24
      h = parseInt(times / 1000 / 3600)
      times = times - h * 1000 * 3600
      m = parseInt(times / 1000 / 60)
      this.timers = [d, h, m]
      console.log(times - m * 1000 * 60)
    }
    this.dataInteractionResponse(data => {
      if (data.msgType === '0x5222' && data.result) {
        let billInfo = data.data
        console.log(billInfo.data)
        let unpayBill = [] // 未支付订单
        if (billInfo.data) {
          billInfo.data.forEach(element => {
            if (element.pay_state === 2) {
              unpayBill.push(element)
            }
          })
        }
        setStorage('unPaybill', unpayBill)
      } else if (data.msgType === '0x5214' && data.result) {
        console.log(this.$route.params.data)
        this.count = parseFloat((this.$route.params.data.fee / 100).toFixed(2))
        this.adress = this.$route.params.data.lentMerchantName
        let d, h, m
        let times = this.$route.params.data.returnTime - this.$route.params.data.lentTime
        console.log(times)
        d = parseInt(times / 1000 / 3600 / 24)
        times = times - d * 1000 * 3600 * 24
        h = parseInt(times / 1000 / 3600)
        times = times - h * 1000 * 3600
        m = parseInt(times / 1000 / 60)
        this.timers = [d, h, m]
        console.log(times - m * 1000 * 60)
      } else if (data.msgType === '0x5224' && data.result) {
        this.count = parseFloat((data.data.fee / 100).toFixed(2))
        this.adress = data.data.return_adress
        let d, h, m
        let ldate = new Date(data.data.lent_time)
        console.log(ldate.getTime())
        let Rdate = new Date(data.data.return_time)
        let times = Rdate.getTime() - ldate.getTime()
        console.log(times)
        d = parseInt(times / 1000 / 3600 / 24)
        times = times - d * 1000 * 3600 * 24
        h = parseInt(times / 1000 / 3600)
        times = times - h * 1000 * 3600
        m = parseInt(times / 1000 / 60)
        this.timers = [d, h, m]
        console.log(times - m * 1000 * 60)
      }
    })
    if (this.ISDEBUG) {
      window.dataInteractionResponse('{"msgType":"0x5224","result":true,"data":{"no":"LD20190115115220","lent_adress":"广东省广州市天河区建中路3号","lent_time":"2019-01-15 11:52:28","return_time":"2019-01-15 11:52:54","return_adress":"广东省广州市天河区建中路3号","duration":"少于1分钟","fee":100,"pay_state":2,"discount_fee":0,"balance_deduction":0,"need_pay_fee":100}}')
    }
  },
  methods: {
    goHandle () {
      this.dataInteractionRequest({
        msgType: '0x5221',
        type: 3,
        page: 1,
        count: 10
      })
      this.$router.push({path: 'home'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.restituteSuccessfully{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    img{
      height: 185px;
      margin: 110px auto 70px auto;
    }
    span{
      font-size: 30px;
      display: block;
    }
    .list{
      .wh(100%, 270px);
      padding: 0 4%;
      margin-top: 8%;
      li{
        .wh-l(100%, 90px);
        span{
          .wh-f(30%, 100%);
          text-align: left;
          font-size: 30px;
          b{
            font-size: 26px;
          }
        }
        strong{
          .wh-f(70%, 100%);
          font-size: 26px;
          text-align: right;
          color: #999;
        }
      }
    }
    button{
      .wh(690px, 100px);
      background: @themeColor;
      border-radius: 10px;
      font-size: 30px;
      color: #5A595A;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -345px;
      // box-shadow: 0px 5px 10px rgba(244, 218, 33, 0.6)
      box-shadow: 0px 4px 25px @themeColor
    }
  }
}
</style>



